<template>
<div>
  <v-layout row wrap>
    <v-flex xs12 class='pa-2'>
      <div style='position: relative; height: 480px;'>
        <v-card>
          <v-carousel height='480' hide-controls hide-delimiters>
            <v-carousel-item v-for='(x, i) in items' :key='i' :src='x.src' />
          </v-carousel>
        </v-card>

        <div style='position: absolute; top: 0; left: 0; width: 100%; height: 100%;'>
          <v-layout row align-center justify-center fill-height>
            <div id='explorerTitle'>
              <span class='title buzzgold--text'>KEN</span>deda building<br />
              <span class='title buzzgold--text'>D</span>igital
              <span class='title buzzgold--text'>E</span>ngineering &amp
              <span class='title buzzgold--text'>D</span>ata
              <span class='title buzzgold--text'>A</span>nalytics

              <div class='affiliation buzzgold--text'>
                <i style='font-size: 1.2rem'>for</i><br />
                <span style='font-size: 2.2rem; line-height: 1.6;' class='buzzgold--text'>Georgia Institute of Technology</span><br />
                <i style='font-size: 1.2rem'>powered by</i><br />
                <span style='font-size: 1.7rem; line-height: 1.6;' class='buzzgold--text'>Aerospace Systems Design Laboratory</span>
              </div>
            </div>
          </v-layout>
        </div>
      </div>
    </v-flex>

    <v-flex xs12 class='pa-2'>
      <v-divider />
    </v-flex>

    <v-flex id='elem' xs6 class='pa-2'>
      <v-card style='overflow: hidden'>
        <iframe :width='vw' :height='vh' src='https://www.youtube.com/embed/4YDdoUPV5fk' frameborder='0' allow='accelerometer; encrypted-media; gyroscope; picture-in-picture' allowfullscreen></iframe>
      </v-card>
    </v-flex>

    <v-flex xs6 class='pa-2'>
      <v-card style='overflow: hidden'>
        <iframe :width='vw' :height='vh' src='https://www.youtube.com/embed/Oh5dhJwM1h8' frameborder='0' allow='accelerometer; encrypted-media; gyroscope; picture-in-picture' allowfullscreen></iframe>
      </v-card>
    </v-flex>

    <v-flex xs12 class='pa-2'>
      <v-divider />
    </v-flex>

    <v-flex v-for='(x, i) in keyDesc' :key='i' xs4 class='pa-2' d-flex>
      <v-card hover class='pa-3 keyCard'>
        <div class='text-xs-center'>
          <v-btn fab outline large color='primary'>
            <v-icon dark>{{ x.icon }}</v-icon>
          </v-btn>
        </div>
        <div v-text='x.title' class='pa-2 bold headline' />
        <div class='pa-2' style='text-align: justify' v-html='x.desc' />
      </v-card>
    </v-flex>
  </v-layout>
</div>
</template>


<script>

export default {
  data: () => ({
    offsetTop: 0,
    items: [
      { src: require('../assets/bldgOverview.jpg') },
      { src: require('../assets/bldgOverview2.jpg') },
      { src: require('../assets/bldgOverview3.jpg') },
    ],
    vw: 560,
    wh: 0,
    ww: 0,
    keyDesc: [
      { icon: 'description', title: "Kendeda living building challenge meets digital engineering", desc: "Digital engineering is one of trending engineering paradigms incorporating conventional physics-based approach with data-driven study.<br /> ASDL's digital enginering technology is finely elaborated to fully optimize the sustainability and net-neutrality of the Kendeda living building." },
      { icon: 'bar_chart', title: "Enhanced Visualization by ASDL's Technology", desc: "For the maximum articulation of data information retrieved from kendeda building, various innovative big-data visualization tools are used by the power of Echarts, a performant and feature-rich javascript library for rapid data visualization and analytics.<br /> Echarts provides not only conventional charts but also lots of advanced visualizations such as sankey diagram, etc." },
      { icon: 'build', title: 'Flexible & Customizable Data Exploration', desc: "KENdeda building Digital Engineeg & Data Analytics is featured with a highly customizable data analysis and exploration framework.<br /> In this framework, a huge amount of complex dataset can be easily analyzed and simulated in various multi-dimensional and simultaneous graphic interface." },
    ]
  }),
  computed: {
    vh() {
      return this.vw * 315 / 560
    }
  },
  methods: {
    onScroll(e) {
      this.offsetTop = e.target.scrollTop
    }
  },
  mounted() {
    const x = document.querySelector('#elem').getBoundingClientRect()
    this.vw = x.width - 16

    this.wh = window.innerHeight
    this.ww = window.innerWidth

    window.addEventListener('scroll', e => {
      this.offsetTop = window.scrollY

    })
  },
}
</script>


<style lang='stylus' scoped>
@import url('https://fonts.googleapis.com/css?family=Roboto')

#explorerTitle
  font-family: 'Roboto' !important
  font-weight: bold
  text-align: center
  font-size: 3rem
  text-shadow: 1px 1px 5px rgba(0, 0, 0, 1)
  color: white
  line-height: 1.2
  text-transform: uppercase

  .title
    // color: #FCCC4A
    font-size: 4.5rem !important

  .affiliation
    margin-top: 2rem
    text-transform: none
    font-size: 1.5rem

    span
      // color: #FCCC4A

.keyCard
  transform: translateY(0)
  transition: all 0.2s

  &:hover
    transform: translateY(-2.5px)

.bold
  font-weight: bold !important
</style>
