<template>
<v-app>
  <VuePerfectScrollbar class='scroll-area' :settings='settings'>
    <AppNavigationDrawer />

    <AppToolbar />

    <v-content>
      <v-layout justify-center>
        <v-flex xs12 lg11 xl10 class='pa-2'>
          <v-slide-y-reverse-transition mode='out-in'>
            <router-view />
          </v-slide-y-reverse-transition>
        </v-flex>
      </v-layout>
    </v-content>

    <AppFooter />
  </VuePerfectScrollbar>
</v-app>
</template>


<script>
import VuePerfectScrollbar from 'vue-perfect-scrollbar'
import AppNavigationDrawer from '@/components/shared/NavigationDrawer'
import AppToolbar          from '@/components/shared/Toolbar'
import AppFooter           from '@/components/shared/Footer'

export default {
  data: () => ({
    settings: {
      minScrollbarLength: 300,
      maxScrollbarLength: 500,
      suppressScrollX: true,
    }
  }),
  created() {
    // this.$vuetify.theme.primary   = '#003057'
    this.$vuetify.theme.primary   = '#C7A754'
    this.$vuetify.theme.techgold  = '#C7A754'
    this.$vuetify.theme.buzzgold  = '#EAAA00'
  },
  watch: {
    '$vuetify.breakpoint'(to) {
      if(to.mdAndDown) _.debounce(() => this.drawer = false, 100)()
    }
  },
  components: {
    VuePerfectScrollbar,
    AppToolbar,
    AppNavigationDrawer,
    AppFooter
  }
}
</script>


<style lang='stylus' scoped>
.scroll-area
  position: relative
  width   : 100%
  height  : 100%
</style>
