<template>
<v-navigation-drawer app fixed clipped v-model='drawer' :width='width'>
  <v-list color='primary'>
    <template v-for='(x, i) in menuItems'>
      <v-list-tile v-if='!x.group' :key='x.to' :to='x.to'>
        <v-list-tile-content>
          <v-list-tile-title v-text='x.title' />
        </v-list-tile-content>
      </v-list-tile>

      <!-- <v-list-group v-else :key='x.title' v-model='x.active' :prepend-icon='x.action' no-action>
        <template v-slot:activator>
          <v-list-tile>
            <v-list-tile-content>
              <v-list-tile-title v-text='x.title' />
            </v-list-tile-content>
          </v-list-tile>
        </template>

        <v-list-tile v-for='y in x.subItems' :key='y.title' :to='y.to'>
          <v-list-tile-content>
            <v-list-tile-title v-text='y.title' />
          </v-list-tile-content>

          <v-list-tile-action>
            <v-icon v-text='y.action' />
          </v-list-tile-action>
        </v-list-tile>
      </v-list-group> -->
    </template>
  </v-list>
</v-navigation-drawer>
</template>


<script>
import { mapState } from 'vuex'

export default {
  data: () => ({
    drawer: false,
  }),
  computed: {
    ...mapState(['menuItems']),
    width() {
      return _.max([window.innerWidth * 0.15, 300])
    }
  },
  beforeMount() {
    // this.drawer = this.$vuetify.breakpoint.lgAndUp

    this.$root.$on('onDrawer', () => {
      this.drawer = !this.drawer
      this.$root.$emit('updateWaterStyle', this.drawer)
    })
  }
}
</script>
